@font-face {
    font-family: "阿里妈妈方圆体 VF Regular";
    font-weight: 600;
    src: url("//at.alicdn.com/wf/webfont/SVFiAuBamSOS/Ur8nhvmyPCPt.woff2") format("woff2"),
    url("//at.alicdn.com/wf/webfont/SVFiAuBamSOS/nqpVboRKQwfv.woff") format("woff");
    font-display: swap;
}

@font-face {
    font-family: "思源黑体 Regular";
    font-weight: 400;
    src: url("//at.alicdn.com/wf/webfont/SVFiAuBamSOS/29rKljwJn4PX.woff2") format("woff2"),
    url("//at.alicdn.com/wf/webfont/SVFiAuBamSOS/XYY9XwdYq9sY.woff") format("woff");
    font-display: swap;
}

.mysql {
    width: 100%;
    height: 100%;
    overflow: scroll;
    background: #f1f1f1;
}

.mysql-form-container {
    height: 100%;
    overflow: scroll;
    background: white;
    width: 500px;
}

.mysql-editor-container {
    flex: 1;
    height: 100%;
}

.mysql-form-item {
    flex: 1;
}

.mysql-connection-panel {
    border: 1px #f1f1f1 solid;
    border-radius: 4px;
    margin: 12px;
    padding: 12px;
}